import { useEffect, useState } from "react";
import { Button, Col, Row } from "react-bootstrap";
import { LinkContainer } from "react-router-bootstrap";
import { useParams } from "react-router-dom";
import ReturnBanner from "../../components/ReturnBanner";
import VideoSection from "../PlantPage/components/videoSection";
const axios = require("axios");

export default function TourPage2() {
  const [tourdata, setTourdata] = useState({
    title: "Loading...",
    text: "Loading...",
    video: ""
  });

  let { lng, tournumber } = useParams();

  let hasVideo = true;

  const fetchdata = async () => {
    const res = await axios.get(
      `https://AcidicDroopyParentheses.roryirwin.repl.co/tour/${lng}/2`
    );
    setTourdata(res.data);
  };

  useEffect(() => {
    fetchdata();
  }, []);

  return (
    <>
      <ReturnBanner returnTo={`/${lng}/tour`} title={tourdata.title} />
      <div style={{ backgroundColor: "red", padding: "15%" }}></div>
      <div>
        <p>{tourdata.text}</p>
      </div>
      {hasVideo ? (
        <div>
          <VideoSection plantname={"lavender"} src={tourdata.video} />
        </div>
      ) : null}

      <Row>
        <Col>
          {tournumber > 1 ? (
            <LinkContainer to={`/${lng}/tour/1`}>
              <Button>Back</Button>
            </LinkContainer>
          ) : null}
        </Col>

        <Col>
          <LinkContainer to={`/${lng}/tour/3`}>
            <Button>Next</Button>
          </LinkContainer>
        </Col>
      </Row>
    </>
  );
}
