import { Row, Col } from "react-bootstrap";
import ReturnButton from "../ReturnButton";
import { useParams } from "react-router-dom";

export default function ReturnBanner(props) {
  let { lng } = useParams();

  return (
    <div className="return-banner">
      <div className="return-banner-inner">
        <Row>
          <Col>
            <ReturnButton returnTo={props.returnTo} />
          </Col>
          <Col>
            <p className="return-banner-title">{props.title}</p>
          </Col>
          <Col></Col>
        </Row>
      </div>
    </div>
  );
}
