import { Button, Container, Row } from "react-bootstrap";
import { LinkContainer } from "react-router-bootstrap";

export default function LanguageSelection() {
  return (
    <Container style={{ maxWidth: "250px" }}>
      <hr />
      <Row>
        <LinkContainer to="/en/home">
          <Button className="button-language language-buttons">English</Button>
        </LinkContainer>
      </Row>
      <div style={{ marginTop: "7px" }} />
      <Row>
        <LinkContainer to="/pl/home">
          <Button className="button-language language-buttons">Polska</Button>
        </LinkContainer>
      </Row>
      <div style={{ marginBottom: "20px" }} />
    </Container>
  );
}
