import axios from "axios";
import { useEffect, useState } from "react";
import ReactPlayer from "react-player";
import { useParams } from "react-router-dom";
import ReturnBanner from "../../components/ReturnBanner";

export default function TourHoldingPage(props) {
  const { lng, tourname } = useParams();

  const [tourdata, setTourdata] = useState({
    title: "Loading...",
    text: ["Loading..."]
  });

  const fetchdata = async () => {
    const res = await axios.get(
      `https://AcidicDroopyParentheses.roryirwin.repl.co/tours/${lng}/${tourname}`
    );
    setTourdata(res.data);
  };
  useEffect(() => {
    fetchdata();
  }, []);

  var textparas = tourdata.text;

  console.log(textparas);

  return (
    <div>
      <ReturnBanner returnTo={`/${lng}/tour`} title={tourdata.title} />
      <div style={{ maxWidth: "400px", margin: "0 auto" }}>
        <ReactPlayer width="100%" url={tourdata.video} />

        {textparas.map((p) => (
          <p style={{ textAlign: "center" }}>{p}</p>
        ))}
      </div>
    </div>
  );
}
