import SLOCard from "../../components/SLOCard";
import { Container, Row, Col, Card, Button } from "react-bootstrap";

import { useParams } from "react-router-dom";

import * as textHome from "../../Text/home";
import LanguageCard from "../../components/LanguageCard";
import { LinkContainer } from "react-router-bootstrap";
import Logo from "../../Logo.png";

export default function Home() {
  let { lng } = useParams();

  return (
    <div id="home-container">
      <div class="hero-div">
        <div class="hero-logo">
          <img class="logo-image" src={Logo} />
        </div>
      </div>
      <div className="home-inner-container">
        <Container style={{ maxWidth: "250px" }}>
          <Row style={{ marginTop: "5%" }}>
            <LinkContainer to={`/${lng}/tour`}>
              <Button className="home-buttons" id="home-button-tour">
                Tour
              </Button>
            </LinkContainer>
            <div style={{ marginTop: "7px" }} />

            <LinkContainer to={`/${lng}/plants`}>
              <Button className="home-buttons" id="home-button-plants">
                Plants
              </Button>
            </LinkContainer>
            <div style={{ marginTop: "7px" }} />

            <LinkContainer to={`/${lng}/tour`}>
              <Button className="home-buttons" id="home-button-sitemap">
                Site Map
              </Button>
            </LinkContainer>
            <div style={{ marginTop: "7px" }} />

            <LinkContainer to={`/${lng}/tour`}>
              <Button className="home-buttons" id="home-button-guess">
                Guess the Scent
              </Button>
            </LinkContainer>
            <div style={{ marginTop: "20px" }} />
          </Row>
        </Container>
      </div>
    </div>
  );
}
