import { useEffect, useState } from "react";
import { Button, Col, Row } from "react-bootstrap";
import { LinkContainer } from "react-router-bootstrap";
import { useParams } from "react-router-dom";
import ReturnBanner from "../../components/ReturnBanner";
import VideoSection from "../PlantPage/components/videoSection";
const axios = require("axios");

export default function TourPage1() {
  const [tourdata, setTourdata] = useState({
    title: "Loading...",
    text: "Loading...",
    video: ""
  });

  const [tourslide, setTourslide] = useState(1);

  let { lng, tournumber } = useParams();

  let hasVideo = true;

  const fetchdata = async () => {
    const res = await axios.get(
      `https://AcidicDroopyParentheses.roryirwin.repl.co/tour/${lng}/1`
    );
    setTourdata(res.data);
  };

  useEffect(() => {
    fetchdata();
  }, []);

  return (
    <>
      <ReturnBanner returnTo={`/${lng}/tour`} title={tourdata.title} />
      <div style={{ backgroundColor: "green", padding: "15%" }}></div>
      <div>
        <p>{tourdata.text}</p>
      </div>
      {hasVideo ? (
        <div>
          <VideoSection plantname={"lavender"} src={tourdata.video} />
        </div>
      ) : null}

      <Row>
        <LinkContainer to={`/${lng}/tour/2`}>
          <Button>Next</Button>
        </LinkContainer>
      </Row>
    </>
  );
}
