import { resolvePath, useParams } from "react-router-dom";
import VideoSection from "./components/videoSection";
import ReturnBanner from "../../components/ReturnBanner";
import { useEffect, useState } from "react";
import { Slide } from "react-slideshow-image";

const axios = require("axios");

export default function PlantPage() {
  const [plantdata, setPlantdata] = useState({
    title: "Loading...",
    description: "Loading..."
  });
  const { lng, plantname } = useParams();

  const fetchdata = async () => {
    const res = await axios.get(
      `https://AcidicDroopyParentheses.roryirwin.repl.co/plants/${lng}/${plantname}`
    );
    setPlantdata(res.data);
  };
  useEffect(() => {
    fetchdata();
  }, []);

  const slideImages = [
    "https://image.shutterstock.com/z/stock-photo-lavender-flowers-isolated-on-white-background-1923549062.jpg",
    "https://image.shutterstock.com/z/stock-photo-lavender-flowers-isolated-on-white-background-1923549062.jpg",
    "https://image.shutterstock.com/z/stock-photo-lavender-flowers-isolated-on-white-background-1923549062.jpg"
  ];

  return (
    <div>
      <ReturnBanner title={plantdata.title} returnTo={`/${lng}/plants`} />
      <div className="plant-page-div">
        <div style={{ textAlign: "center" }}>
          {/* back to plants button */}
          {/* Header image */}

          {/* Plant Video */}
          <VideoSection src={plantdata.video} plantname={plantname} />
          {/* Plant description */}
          <div style={{ margin: "5%" }}>
            <p>{plantdata.description}</p>
          </div>
          <div>
            <h3>Plant Smell</h3>
            <p>plant small include</p>
          </div>
        </div>
      </div>
    </div>
  );
}
