import SLOCard from "../../components/SLOCard";
import { Container, Row, Col, Card, Button } from "react-bootstrap";
import { BsMap, BsFillCameraVideoFill } from "react-icons/bs";
import { FaProductHunt } from "react-icons/fa";
import { RiPlantLine } from "react-icons/ri";
import { TiCogOutline } from "react-icons/ti";
import { BiTimeFive } from "react-icons/bi";
import { useParams } from "react-router-dom";
import * as textTourHome from "../../Text/tourHome";
import { LinkContainer } from "react-router-bootstrap";
import ReturnButton from "../../components/ReturnButton/index";
import ReturnBanner from "../../components/ReturnBanner";

export default function EnvironmentalTour() {
  let { lng } = useParams();

  return (
    <>
      <ReturnBanner returnTo={`/${lng}/tour`} title="Environmental" />

      <Container style={{ maxWidth: "250px" }}>
        <Row>
          <div style={{ marginTop: "7px" }} />
          <LinkContainer
            to={`/${lng}/tours/environmental/rewilding`}
            style={{}}
          >
            <Button>Rewilding</Button>
          </LinkContainer>
        </Row>
        <Row>
          <div style={{ marginTop: "7px" }} />
          <LinkContainer to={`/${lng}/tours/environmental/tarhill`} style={{}}>
            <Button>Tarhill Farm</Button>
          </LinkContainer>
        </Row>
        <Row>
          <div style={{ marginTop: "7px" }} />
          <LinkContainer to={`/${lng}/tours/environmental/llnnr`} style={{}}>
            <Button>Living beside a National Nature Reserve</Button>
          </LinkContainer>
        </Row>
        <Row>
          <div style={{ marginTop: "7px" }} />
          <LinkContainer to={`/${lng}/tours/environmental/wildlife`} style={{}}>
            <Button>Our Wildlife</Button>
          </LinkContainer>
        </Row>
        <div style={{ marginTop: "7px" }} />
      </Container>
    </>
  );
}
